@import "./index/search-bar.scss";
@import "./index/main-nav.scss";
@import "./index/featured.scss";
@import "./index/search-results.scss";
@import "./index/chart.scss";

$lightbrown: #743e28;
$darkbrown: #bb8c5d;
$lightgray: #AAA7A6;
$white: white;

body, html {
  background: $white;
  height: 100%;
}

.container {
  height: auto;
  width: 100%;
  top: 0px;
  left: 0px;
}

.container-center {
  position: relative;
  height: 100vh;
  width: 80%;
  margin:0 auto;
  display: flex;
  flex-direction: column;
}

.left{
  position: absolute;
  background: url(../dist/assets/pawprint1.png);
  background-size: 100px;
  background-repeat: round;
  height: 100%;
  width: 10%;
  top: 50px;
}

.right{
  position: absolute;
  background: url(../dist/assets/pawprint2.png);
  background-size: 100px;
  background-repeat: round;
  height: 100%;
  width: 10%;
  top: 50px;
  right: 0px;
}

#btn:hover {
  cursor: pointer;
}

.container-bottom {
  height: 100%;
}
